const {
  ElMessage
} = require("element-plus");

export default {
  name: "LoginPage",

  data() {
    return {
      loginForm: {
        username: "liwei",
        password: "lw1234"
      },
      passwordType: "password",
      rules: {
        username: [{
          required: true,
          message: 'Please input Activity name',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please input Activity name',
          trigger: 'blur'
        }]
      }
    };
  },

  methods: {
    login() {
      // 1、拿到username和password
      // 2、使用axios发送登录请求给后端（SpringBoot）
      console.log("用户密码：", this.loginForm);
      this.$api.login.login("/login", this.loginForm).then(res => {
        console.log("服务器返回的结果", res); // vuex（状态管理）
        //把token给存储起来

        console.log(res.token);
        sessionStorage.setItem("token", res.token); // 跳转到页面之前要调用后台的菜单接口，获取当前用户的菜单
        // 再次发请求，但是发请求之前，要带上token

        this.$api.menu.findNavTree("menu/findNavTree", {
          'userName': this.loginForm.username
        }).then(res => {
          console.log("当前用户的菜单：", res); //this.$store.commit('setNavTree',res);
          // this.$store.dispatch('setNavTree',{res});//分发完毕后
          // 同步的话：下面还有其他代码，就暂时无法执行

          this.$store.dispatch({
            // 给全局属性赋值，dispatch是异步赋值
            type: 'setNavTree',
            //这里type是个函数，必须和actions中的一样
            data: res
          });
          return new Promise((resolve, reject) => {
            let a = false;
            resolve(this.loginForm.username);

            if (a) {
              reject("出错了");
            }
          });
        }).then(res => {
          console.log("准备获取用户的权限：", res);
          this.$api.user.findPermissions("user/findPermissions", {
            'name': res
          }).then(res => {
            console.log(this.loginForm.username + "的权限：" + res);
            this.$store.commit('setPerms', res);
          });
        });
        this.$router.push("/index");
      }).catch(err => {
        console.log(err);
        this.$router.push("/"); //跳到登录页面
      });
    },

    changeState() {
      if (this.passwordType === 'password') {
        this.passwordType = 'text';
      } else {
        this.passwordType = 'password';
      }
    }

  }
};