export default {
  name: "SlideShowPage",

  data() {
    return {
      tableData0: [],
      tableData1: [{
        name: '优惠1',
        url: 'http://120.79.91.8/images/yks/优惠1.png'
      }, {
        name: '优惠2',
        url: 'http://120.79.91.8/images/yks/优惠2.png'
      }, {
        name: '优惠3',
        url: 'http://120.79.91.8/images/yks/优惠3.png'
      }]
    };
  },

  mounted: function () {
    this.getShowVue();
  },
  methods: {
    insertFile() {
      let file = new FormData();
      file.append("file", this.file);
      this.$api.file.upload('/file/insertFile', file, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        this.$message.success('文件上传成功');
      }).catch(res => {
        this.$message.error('文件上传失败');
      });
    },

    deleteFile(id) {
      this.$api.file.deleteFile('file/deleteFile', {
        'ID': id
      }).then(res => {
        this.$message.success('修改成功');
      });
      console.log(id);
    },

    changAble(id, enable) {
      this.$api.file.changAble('file/changAble', {
        'ID': id,
        'enable': enable
      }).then(res => {
        this.$message.success('修改成功');
      });
    },

    getShowVue() {
      this.$api.file.getShowVue('file/getShowVue', {
        'ID': ""
      }).then(res => {
        this.tableData0 = res;
        this.$message.success('获取图片成功');
      });
    },

    upload() {
      let file = new FormData();
      file.append("file", this.file);
      file.append("filename", this.filename);
      this.$api.file.upload('/file/upload', file, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(res => {
        this.$message.success('文件上传成功');
      }).catch(res => {
        this.$message.error('文件上传失败');
      });
    },

    getFile(event, idx) {
      this.filename = idx;
      this.file = event.target.files[0];
      let fileName = this.file.name;
      let index = fileName.lastIndexOf(".");
      let fileType = ['png'];

      if (index != -1) {
        let suffix = fileName.substr(index + 1).toLowerCase();

        if (fileType.includes(suffix)) {
          this.$message.success('文件添加成功');
        } else {
          this.$message.error("文件格式错误！请选择 'png' 格式的文件");
        }
      }
    }

  }
};