export default {
  name: "IndexHello",

  data() {
    return {
      currentIndex: 0,
      //当前所在图片下标
      timer: null,
      //定时轮询
      imgArr: [{
        id: 0,
        url: 'http://g7.glypro19.com:8088/photo/Souye1.webp'
      }, {
        id: 1,
        url: " http://g7.glypro19.com:8088/photo/Souye2.webp"
      }, {
        id: 2,
        url: "http://g7.glypro19.com:8088/photo/Souye3.webp"
      }, {
        id: 3,
        url: "http://g7.glypro19.com:8088/photo/Souye4.webp"
      }]
    };
  },

  methods: {
    //开启定时器
    startInterval() {
      // 事件里定时器应该先清除在设置，防止多次点击直接生成多个定时器
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.currentIndex++;

        if (this.currentIndex > this.imgArr.length - 1) {
          this.currentIndex = 0;
        }
      }, 3000);
    },

    // 点击左右箭头
    clickIcon(val) {
      if (val === 'down') {
        this.currentIndex++;

        if (this.currentIndex === this.imgArr.length) {
          this.currentIndex = 0;
        }
      } else {
        /* 第一种写法
        this.currentIndex--;
        if(this.currentIndex < 0){
          this.currentIndex = this.imgArr.length-1;
        } */
        // 第二种写法
        if (this.currentIndex === 0) {
          this.currentIndex = this.imgArr.length;
        }

        this.currentIndex--;
      }
    },

    // 点击控制圆点
    changeImg(index) {
      this.currentIndex = index;
    },

    //鼠标移入移出控制
    changeInterval(val) {
      if (val) {
        clearInterval(this.timer);
      } else {
        this.startInterval();
      }
    }

  },

  //进入页面后启动定时轮询
  mounted() {
    this.startInterval();
  }

};