import { hasPermission } from "@/permissions";
import { Axios as axios } from "axios";
export default {
  name: "UserPage",

  data() {
    return {
      users: [],
      new_password: null
    };
  },

  mounted: function () {
    this.getUser();
  },
  methods: {
    getUser() {
      this.$api.user.getUser("user/getUser").then(res => {
        console.log(res);
        let i;

        for (i = 0; this.users[i];) {
          this.users.splice(0, 1);
        }

        for (i = 0; res[i]; i++) this.users.push({
          id: res[i].id,
          name: res[i].name,
          password: res[i].password,
          nickName: res[i].nickName,
          isFlag: true
        });
      });
    },

    addUser() {
      let params = {
        name: this.name,
        password: this.password
      };
      console.log(params.name);
      console.log(params.password);
      console.log(params);
      this.$api.user.addUser("user/addUser", {
        "name": params.name,
        "password": params.password,
        "nick_name": params.name
      }).then(res => {
        this.getUser();
        console.log(res);
      });
    },

    updateUser(user) {
      user.password = this.new_password;
      this.$api.user.updateUser("user/updateUser", {
        "id": user.id,
        "name": user.name,
        "password": user.password,
        "nick_name": user.nickName
      }).then(res => {
        this.getUser();
        console.log(res);
      });
    }

  },
  computed: {}
};