/* unplugin-vue-components disabled */const {
  ElMessageBox
} = require("element-plus");

export default {
  name: "foodMange",

  data() {
    return {
      loginForm: {
        username: 'liwei',
        password: 'lw1234'
      },
      userList: [],
      formData: {
        product_id: "",
        product_category: "",
        product_name: "",
        price: "",
        img: "",
        cost_price: ""
      },
      select: "",
      input: "",
      border: true,
      loading: false,
      url: 'https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg',
      dialogFormVisible: false,
      //新增
      dialogFormEVisible: false,
      //编辑
      value: "",
      options: [{
        value: '美味小食',
        label: '美味小食'
      }, {
        value: '缤纷饮品',
        label: '缤纷饮品'
      }, {
        value: '招牌全鸡',
        label: '招牌全鸡'
      }, {
        value: '精选主食',
        label: '精选主食'
      }, {
        value: '超值套餐',
        label: '超值套餐'
      }],
      rules: {
        product_id: [{
          required: true,
          message: "此项为必填",
          trigger: "blur"
        }],
        product_category: [{
          required: true,
          message: "此项为必填",
          trigger: "blur"
        }],
        product_name: [{
          required: true,
          message: "此项为必填",
          trigger: "blur"
        }],
        price: [{
          required: true,
          message: "此项为必填",
          trigger: "blur"
        }]
      }
    };
  },

  mounted: function () {
    this.showData();
  },
  methods: {
    showData() {
      this.loading = true;
      this.$api.food.getFoodList("food/foodList", {
        'abc': ""
      }).then(res => {
        console.log("商品items：", res);
        console.log("商品来咯：");
        this.userList = res;
        this.loading = false;
      }).catch(err => {
        console.log(err);
      });
    },

    addUser() {
      this.dialogFormVisible = true;
    },

    hideDialog() {
      this.dialogFormVisible = false;
      this.formData = {
        product_id: "",
        product_category: "",
        product_name: "",
        price: "",
        img: "",
        cost_price: ""
      };
    },

    //添加新品
    submitForm() {
      this.formData.product_category = this.value;

      if (this.formData.product_id === "" || this.formData.product_name === "" || this.formData.product_category === "" || this.formData.cost_price === "" || this.formData.img === "" || this.formData.price === "") {
        // ElMessage.error('请补充完整餐品信息');
        ElMessage({
          message: '请补充完整餐品信息',
          type: 'error',
          duration: 2000
        });
      } else {
        console.log("新品添加表单--------->", this.formData);
        this.$api.addFood.addFood("food/addFood", this.formData).then(res => {
          console.log("请求添加新品返回的数据------------>", res);
          console.log("请求添加新品返回的数据product_id-------->", res.product_id);

          if (res.product_id) {
            ElMessage({
              message: '添加成功',
              type: 'success',
              duration: 2000
            }); // 隐藏弹窗

            this.dialogFormVisible = false; // 清空form

            this.formData = {
              product_id: "",
              product_category: "",
              product_name: "",
              price: "",
              img: "",
              cost_price: ""
            };
            this.value = ""; // 重新更新列表

            this.loading = true;
            this.showData();
          }
        }).catch(err => {
          console.log(err);
        });
      }
    },

    editRow(row) {
      this.dialogFormEVisible = true;
      console.log("当前table一行的数据--------->", this.formData);
      this.formData = row;
      console.log("当前table一行的数据--------->", this.formData);
    },

    editSubmitForm() {
      console.log("修改", this.formData);
      this.$api.editFood.editFood("food/editFood", this.formData).then(res => {
        console.log("修改后的数据------>", res);
        ElMessage({
          message: '保存成功',
          type: 'success',
          duration: 2000
        });
        this.dialogFormEVisible = false;
        this.formData = {
          product_id: "",
          product_category: "",
          product_name: "",
          price: "",
          img: "",
          cost_price: ""
        };
      });
    },

    hideEditDialog() {
      // this.dialogFormEVisible = false;
      console.log("取消编辑", this.formData);
      this.formData = {
        product_id: "",
        product_category: "",
        product_name: "",
        price: "",
        img: "",
        cost_price: ""
      };
      this.dialogFormEVisible = false;
      console.log("取消编辑", this.formData);
    },

    searchList() {
      console.log("input内容", this.input);

      if (this.input) {
        this.loading = true;
        this.$api.selectFood.selectFood("food/selectFood", {
          'parameter': this.input
        }).then(res => {
          ElMessage({
            message: '查询成功',
            type: 'success',
            duration: 2000
          });
          console.log("选择的数据------->", res);
          this.userList = res;
          this.loading = false;
        });
      } else {
        ElMessage({
          message: '请输入查询信息',
          type: 'error',
          duration: 2000
        });
      }
    },

    selectPro() {
      this.loading = true;
      console.log("选择了-------", this.select);
      this.$api.optionFood.optionFood("food/optionFood", {
        'parameter': this.select
      }).then(res => {
        console.log("分来来咯", res);
        this.userList = res;
        this.loading = false;
      });
    },

    deleteRow(row) {
      console.log("删除的行数据", row);
      ElMessageBox.confirm('确定删除该餐品吗?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$api.deleteFood.deleteFood("food/deleteFood", {
          'parameter': row.product_id
        }).then(res => {
          console.log("删除咯", res);
          this.showData();
        });
        ElMessage({
          type: 'success',
          message: '删除成功'
        });
      }).catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled'
        });
      });
    }

  }
};