import { findMenu } from "../http/modules/menu";
export default {
  name: "MenuPage",

  data() {
    return {
      submenu: {
        submenuHide: true,
        submenuShow: false
      },
      tableData: [],
      form: {
        id: '',
        type: '',
        name: '',
        price: '',
        nums: '',
        operation: ''
      },
      options: [{
        value: '选项1',
        label: '美味小吃'
      }, {
        value: '选项2',
        label: '精选主食'
      }, {
        value: '选项3',
        label: '招牌全鸡'
      }, {
        value: '选项4',
        label: '特惠套餐'
      }, {
        value: '选项5',
        label: '缤纷饮品'
      }],
      value: ''
    };
  },

  mounted: function () {
    this.findMenu(); //需要触发的函数
  },
  methods: {
    findMenu() {
      this.$api.menu.findMenu("dishes/findMenu").then(res => {
        console.log("................", res);
        let i;

        for (i = 0; this.tableData[i];) {
          this.tableData.splice(0, 1);
        }

        for (i = 0; res[i]; i++) {
          this.tableData.push({
            id: res[i].id,
            type: res[i].type,
            name: res[i].name,
            price: res[i].price,
            num: res[i].nums
          });
        }
      });
    },

    insert() {
      this.submenu.submenuHide = false;
      this.submenu.submenuShow = true;
      this.form.id = '0';
      this.form.type = '';
      this.form.name = '';
      this.form.price = '';
      this.form.nums = '';
      this.form.operation = 1;
    },

    confirm() {
      this.$confirm('确定要修改此信息吗！', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        if (this.form.operation == 0) {
          this.$api.menu.modifyMenu("dishes/modifyMenu", {
            'id': this.form.id,
            'type': this.form.type,
            'name': this.form.name,
            'price': this.form.price,
            'nums': this.form.nums
          }).then(res => {
            this.findMenu();
          });
        } else if (this.form.operation == 1) {
          this.$api.menu.insertMenu("dishes/insertMenu", {
            'id': this.form.id,
            'type': this.form.type,
            'name': this.form.name,
            'price': this.form.price,
            'nums': this.form.nums
          }).then(res => {
            this.findMenu();
          });
        }

        this.$message({
          type: 'success',
          message: '修改成功!'
        });
        this.cancel();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        });
        this.cancel();
      });
    },

    selectMenu(value) {
      this.$api.menu.selectMenu("dishes/selectMenu", {
        'type': value
      }).then(res => {
        let i;

        for (i = 0; this.tableData[i];) {
          this.tableData.splice(0, 1);
        }

        for (i = 0; res[i]; i++) {
          this.tableData.push({
            id: res[i].id,
            type: res[i].type,
            name: res[i].name,
            price: res[i].price,
            num: res[i].nums
          });
        }
      });
    },

    handleEdit(index, row) {
      this.submenu.submenuHide = false;
      this.submenu.submenuShow = true;
      this.form.id = row.id;
      this.form.type = row.type;
      this.form.name = row.name;
      this.form.price = row.price;
      this.form.nums = row.num;
      this.form.operation = 0;
    },

    cancel() {
      this.submenu.submenuHide = true;
      this.submenu.submenuShow = false;
    },

    handleDelete(index, row) {
      console.log("index...", index);
      console.log(row.id);
      this.$confirm('此操作将永久删除该信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$api.menu.deleteMenu("dishes/deleteMenu", {
          'id': row.id
        }).then(res => {
          console.log(res);
          this.tableData.splice(index, 1);
        });
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
    /*findMenu(){
        this.$api.menu.findMenu("dishes/findMenu")
            .then(res=>{
                console.log("................",res);
                let i;
                for(i=0;this.tableData[i];){
                    this.tableData.splice(0,1);
                }
                for(i=0;res[i];i++){
                    this.tableData.push({id:res[i].id,type: res[i].type, name: res[i].name,price: res[i].price, num: res[i].nums});
                }
              })
    }*/


  }
};